import { useState, useEffect } from 'react';

// Define Company interface
interface Company {
  name: string;
  color: string;
}

export default function InfiniteScrollingCarousel() {
  const companies: Company[] = [
    { name: "Mistral", color: "bg-blue-600" },
    { name: "Anthropic", color: "bg-purple-600" },
    { name: "Google", color: "bg-red-500" },
    { name: "DeepSeek", color: "bg-green-600" },
    { name: "Open", color: "bg-yellow-500" },
    { name: "OpenAI", color: "bg-teal-500" },
    { name: "Meta AI", color: "bg-blue-500" }
  ];

  // Duplicate the companies array to ensure continuous scrolling
  const duplicatedCompanies: Company[] = [...companies, ...companies, ...companies];

  const [scrollPosition1, setScrollPosition1] = useState(0);
  const [scrollPosition2, setScrollPosition2] = useState(0);

  useEffect(() => {
    const scrollSpeed1 = 0.5;
    const scrollSpeed2 = 0.35;
    
    const animate = () => {
      setScrollPosition1(prev => {
        // Reset to start when the first set of items has scrolled out of view
        if (prev >= 100) return 0;
        return prev + scrollSpeed1;
      });
      
      setScrollPosition2(prev => {
        // Reset to end when the first set of items has scrolled into view
        if (prev <= -100) return 0;
        return prev - scrollSpeed2;
      });
      
      animationRef.current = requestAnimationFrame(animate);
    };
    
    const animationRef = { current: requestAnimationFrame(animate) };
    
    return () => {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    };
  }, []);

  const getLogoPlaceholder = (company: Company) => {
    return (
      <div className={`${company.color} text-white font-bold h-12 w-12 rounded-full flex items-center justify-center`}>
        {company.name.charAt(0)}
      </div>
    );
  };

  return (
    <div className="w-full overflow-hidden bg-gray-50 py-12">
      <h2 className="text-3xl font-bold text-center mb-12">AI Companies</h2>
      
      {/* First carousel - scrolling right */}
      <div className="relative w-full overflow-hidden mb-8">
        <div 
          className="flex items-center" 
          style={{ transform: `translateX(-${scrollPosition1}%)` }}
        >
          {duplicatedCompanies.map((company, index) => (
            <div 
              key={`${company.name}-${index}`}
              className="flex flex-col items-center justify-center mx-8 min-w-32"
            >
              {getLogoPlaceholder(company)}
              <span className="mt-2 font-medium">{company.name}</span>
            </div>
          ))}
        </div>
      </div>
      
      {/* Second carousel - scrolling left */}
      <div className="relative w-full overflow-hidden">
        <div 
          className="flex items-center" 
          style={{ transform: `translateX(${scrollPosition2}%)` }}
        >
          {duplicatedCompanies.map((company, index) => (
            <div 
              key={`${company.name}-reverse-${index}`}
              className="flex flex-col items-center justify-center mx-8 min-w-32"
            >
              {getLogoPlaceholder(company)}
              <span className="mt-2 font-medium">{company.name}</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}